:root {
  --layout-background: url("https://images.unsplash.com/photo-1682685797769-481b48222adf?ixlib=rb-4.0.3&ixid=M3wxMjA3fDF8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1170&q=80");
  --layout-docker-divider-line-style: 1px solid white;
  --layout-docker-padding: 8px;
  --layout-docker-redius: 12px;
  --layout-docker-bg: rgba(255, 255, 255, 1);
  --layout-docker-bg-blur: 64px;
  --layout-docker-margin-bottom: 15px;
  --layout-docker-hidden-bg: linear-gradient(to right, blue, red);
  --layout-docker-item-size: 35px;
  --layout-docker-item-radius: 50% 40%;
  --layout-docker-item-padding: 12px;
  --layout-docker-item-margin-bottom: 15px;
}

.view {
  width: 100vw;
  height: 100vh;
  position: relative;
  background: var(--layout-background);
  background-size: cover;
  background-repeat: no-repeat;
  overflow-x: hidden;
  overflow-y: hidden !important;
}
.view main.content {
  width: 100%;
  overflow-x: hidden;
  overflow-y: hidden !important;
}
.view footer.hidden .docker {
  top: calc(var(--layout-docker-item-size) + var(--layout-docker-item-padding) * 2 + var(--layout-docker-padding) + 2px + var(--layout-docker-item-margin-bottom)) !important;
  background: var(--layout-docker-hidden-bg) !important;
  max-width: 300px !important;
}
.view footer.footer {
  position: absolute;
  padding-bottom: var(--layout-docker-margin-bottom);
  bottom: 0;
  user-select: none;
  width: 100%;
  min-width: unset;
  max-width: unset;
  height: unset;
  min-height: 40px;
  max-height: unset;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 1;
  transition: all 0.5s ease-out;
}
.view footer.footer .docker {
  position: relative;
  top: 0;
  background-color: unset;
  border-radius: var(--layout-docker-redius);
  padding: var(--layout-docker-padding) 0;
  height: calc(var(--layout-docker-item-size) + var(--layout-docker-item-padding) * 2);
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  flex-shrink: 1;
  max-width: unset;
  flex-flow: row;
  animation: all 0.5s ease-out;
}
.view footer.footer .docker div, .view footer.footer .docker section {
  z-index: 1;
}
.view footer.footer .docker div.back {
  width: 100%;
  height: 100%;
  border-radius: var(--layout-docker-redius);
  animation: all 0.5s ease-out;
  position: absolute;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: hidden !important;
}
.view footer.footer .docker div.back::before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: var(--layout-docker-bg);
  filter: blur(var(--layout-docker-bg-blur)) opacity(0.8) contrast(100%);
  z-index: 1;
  animation: all 0.5s ease-out;
  position: absolute;
  top: 0;
  left: 0;
}
.view footer.footer .docker section.dockerItem {
  flex-shrink: 0;
  width: var(--layout-docker-item-size);
  height: var(--layout-docker-item-size);
  border-radius: var(--layout-docker-item-radius);
  padding: var(--layout-docker-item-padding);
  margin-left: calc(var(--layout-docker-item-size) / 2);
  margin-bottom: calc(var(--i) * var(--layout-docker-item-margin-bottom));
  white-space: nowrap;
  transition: all 0.2s ease-out;
  background-size: 140% 140%;
  background-position: center center;
  background-repeat: no-repeat;
  overflow-x: hidden;
  overflow-y: hidden !important;
}
.view footer.footer .docker section.dockerItem:last-child {
  margin-right: calc(var(--layout-docker-item-size) / 2);
}
.view footer.footer .docker div.driverLine {
  margin-left: calc(var(--layout-docker-item-size) / 2);
  width: 0;
  height: calc(var(--layout-docker-item-size) + var(--layout-docker-item-padding) + var(--layout-docker-padding));
  border-left: var(--layout-docker-divider-line-style);
}